import React, { useState, useEffect } from 'react';
import { Button, Flex, Text } from '@fluentui/react-northstar';
import * as microsoftTeams from "@microsoft/teams-js";
import './App.css';

function App() {
  const [timeLeft, setTimeLeft] = useState(0);
  const [isRunning, setIsRunning] = useState(false);
  const [isPaused, setIsPaused] = useState(false);
  const [inputMinutes, setInputMinutes] = useState('');
  const [inputSeconds, setInputSeconds] = useState('');
  const [isOrganizer, setIsOrganizer] = useState(false);
  const [isTeamsInitialized, setIsTeamsInitialized] = useState(false);
  const [error, setError] = useState(null);
  const [hasTimerControl, setHasTimerControl] = useState(false);
  const [userRole, setUserRole] = useState(null);
  const [permissions, setPermissions] = useState({
    organizerOnly: true,
    presenters: false,
    coorganizers: false
  });
  const [debugContext, setDebugContext] = useState({
    userId: '',
    organizerId: '',
    meetingId: '',
    roles: []
  });

  useEffect(() => {
    const initializeTeams = async () => {
      try {
        // Check if we're running in Teams
        if (window.parent === window.self) {
          console.log('Running outside of Teams - skipping Teams initialization');
          setHasTimerControl(true); // Allow control when running outside Teams
          return;
        }

        await microsoftTeams.app.initialize();
        setIsTeamsInitialized(true);

        try {
          // Get user context first
          const userContext = await microsoftTeams.app.getContext();
          console.log('User context:', userContext);

          // Get meeting context
          const meetingContext = await microsoftTeams.meeting.getMeetingDetails();
          console.log('Meeting context:', meetingContext);

          // Get configuration
          try {
            const configContext = await microsoftTeams.pages.config.getConfig();
            console.log('Config context:', configContext);
            
            if (configContext?.userConfigs) {
              setPermissions(configContext.userConfigs);
            }
          } catch (configError) {
            console.log('Error getting config (using defaults):', configError);
          }

          // Store context for debugging
          setDebugContext({
            userId: userContext.user?.id || 'Unknown',
            organizerId: meetingContext.meeting?.organizer?.id || 'Unknown',
            meetingId: meetingContext.meeting?.id || 'Unknown',
            roles: meetingContext.meeting?.roles || []
          });

          // Check if user is organizer
          const isOrg = userContext.user?.id === meetingContext.meeting?.organizer?.id;
          console.log('Organizer check:', {
            userId: userContext.user?.id,
            organizerId: meetingContext.meeting?.organizer?.id,
            isOrganizer: isOrg
          });
          setIsOrganizer(isOrg);

          // Get user role
          let currentUserRole = null;
          if (meetingContext.meeting?.roles) {
            // First try to get role directly from meeting roles
            const userRoleObj = meetingContext.meeting.roles.find(
              role => role.userId === userContext.user?.id || role.id === userContext.user?.id
            );
            if (userRoleObj) {
              currentUserRole = userRoleObj.role?.toLowerCase();
            } else {
              // Fallback: check if user is in the meeting participant list
              const participant = meetingContext.meeting?.conversation?.participants?.find(
                p => p.user?.id === userContext.user?.id
              );
              if (participant) {
                currentUserRole = participant.role?.toLowerCase();
              }
            }
          }

          console.log('Role detection:', {
            currentUserRole,
            availableRoles: meetingContext.meeting?.roles,
            participants: meetingContext.meeting?.conversation?.participants
          });

          setUserRole(currentUserRole);

          // Determine permissions
          if (isOrg) {
            setHasTimerControl(true);
            console.log('Granting control to organizer');
          } else {
            const hasControl = !permissions.organizerOnly && (
              (permissions.presenters && currentUserRole === 'presenter') ||
              (permissions.coorganizers && currentUserRole === 'coorganizer')
            );
            setHasTimerControl(hasControl);
            console.log('Permission check:', {
              hasControl,
              permissions,
              currentUserRole
            });
          }

        } catch (meetingError) {
          console.error('Error getting meeting details:', meetingError);
          setHasTimerControl(false);
        }

        // Register handlers
        microsoftTeams.app.registerOnThemeChangeHandler((theme) => {
          console.log('Theme changed:', theme);
        });

        try {
          await microsoftTeams.messages.registerMessageHandler("timerUpdate", (message) => {
            if (message.timeLeft !== undefined) setTimeLeft(message.timeLeft);
            if (message.isRunning !== undefined) setIsRunning(message.isRunning);
            if (message.isPaused !== undefined) setIsPaused(message.isPaused);
          });
        } catch (messageError) {
          console.log('Error registering message handler:', messageError);
        }
      } catch (err) {
        console.error('Teams initialization error:', err);
        setError(err.message);
      }
    };

    initializeTeams();
  }, []);

  useEffect(() => {
    let timer;
    if (isRunning && !isPaused && timeLeft > 0) {
      timer = setInterval(() => {
        setTimeLeft((prevTime) => {
          const newTime = prevTime - 1;
          if (isTeamsInitialized && hasTimerControl) {
            broadcastTimerState(newTime, isRunning, isPaused);
          }
          return newTime;
        });
      }, 1000);
    } else if (timeLeft === 0) {
      setIsRunning(false);
      setIsPaused(false);
      if (isTeamsInitialized && hasTimerControl) {
        broadcastTimerState(0, false, false);
      }
    }
    return () => clearInterval(timer);
  }, [isRunning, isPaused, timeLeft, hasTimerControl, isTeamsInitialized]);

  const broadcastTimerState = async (time, running, paused) => {
    try {
      if (!isTeamsInitialized) return;

      const context = await microsoftTeams.meeting.getMeetingDetails();
      const participants = context.conversation.conversationParticipants || [];
      
      for (const participant of participants) {
        try {
          await microsoftTeams.messages.sendMessage({
            message: {
              timeLeft: time,
              isRunning: running,
              isPaused: paused
            },
            messageTarget: "timerUpdate",
            participantId: participant.user.id
          });
        } catch (err) {
          console.log('Error sending message to participant:', err);
        }
      }
    } catch (err) {
      console.log('Error broadcasting timer state:', err);
    }
  };

  const startTimer = () => {
    const minutes = parseInt(inputMinutes) || 0;
    const seconds = parseInt(inputSeconds) || 0;
    
    if (minutes >= 0 && seconds >= 0 && (minutes > 0 || seconds > 0)) {
      const newTime = (minutes * 60) + seconds;
      setTimeLeft(newTime);
      setIsRunning(true);
      setIsPaused(false);
      setInputMinutes('');
      setInputSeconds('');
      if (isTeamsInitialized && hasTimerControl) {
        broadcastTimerState(newTime, true, false);
      }
    }
  };

  const stopTimer = () => {
    setIsRunning(false);
    setIsPaused(false);
    setTimeLeft(0);
    if (isTeamsInitialized && hasTimerControl) {
      broadcastTimerState(0, false, false);
    }
  };

  const togglePause = () => {
    const newPausedState = !isPaused;
    setIsPaused(newPausedState);
    if (isTeamsInitialized && hasTimerControl) {
      broadcastTimerState(timeLeft, isRunning, newPausedState);
    }
  };

  const formatTime = (seconds) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  const handleInputChange = (value, type) => {
    // Remove any non-numeric characters
    const numericValue = value.replace(/[^0-9]/g, '');
    
    if (type === 'minutes') {
      setInputMinutes(numericValue);
    } else {
      // Ensure seconds are between 0 and 59
      const seconds = parseInt(numericValue) || 0;
      if (seconds <= 59) {
        setInputSeconds(numericValue);
      }
    }
  };

  if (error) {
    return (
      <div className="app-container">
        <Text error content={`Error: ${error}`} />
        <Text content="The app will work with limited functionality." />
      </div>
    );
  }

  return (
    <div className="app-container">
      <Text size="large" weight="bold" content="Meeting Timer" />
      
      <div className={`timer-display ${timeLeft <= 30 && !isPaused ? 'timer-red' : ''}`}>
        {formatTime(timeLeft)}
      </div>

      {(!isTeamsInitialized || hasTimerControl) ? (
        <div className="controls">
          <div className="time-inputs">
            <div className="time-input-container">
              <label htmlFor="minutes">Minutes</label>
              <input
                id="minutes"
                type="number"
                min="0"
                placeholder="0"
                value={inputMinutes}
                onChange={(e) => handleInputChange(e.target.value, 'minutes')}
                disabled={isRunning}
              />
            </div>
            <div className="time-input-container">
              <label htmlFor="seconds">Seconds</label>
              <input
                id="seconds"
                type="number"
                min="0"
                max="59"
                placeholder="0"
                value={inputSeconds}
                onChange={(e) => handleInputChange(e.target.value, 'seconds')}
                disabled={isRunning}
              />
            </div>
          </div>
          
          <div className="button-group">
            <Button
              primary
              content={isRunning ? "Running..." : "Start Timer"}
              onClick={startTimer}
              disabled={isRunning || (!inputMinutes && !inputSeconds)}
            />
            {isRunning && (
              <Button
                content={isPaused ? "Resume" : "Pause"}
                onClick={togglePause}
                style={{
                  backgroundColor: isPaused ? '#5cb85c' : '#f0ad4e',
                  color: 'white'
                }}
              />
            )}
            <Button
              content="Stop Timer"
              onClick={stopTimer}
              disabled={!isRunning}
              style={{
                backgroundColor: '#d9534f',
                color: 'white'
              }}
            />
          </div>
        </div>
      ) : (
        <Text size="small" content={
          isOrganizer ? 
            "Timer controls are currently restricted to specific roles. Update the app settings to modify permissions." :
            "You don't have permission to control the timer. Only authorized users can control the timer."
        } />
      )}

      {isPaused && (
        <div className="pause-indicator">
          Timer Paused
        </div>
      )}

      {hasTimerControl && (
        <div className="role-indicator">
          <Text size="small" content={`You have timer control as: ${isOrganizer ? 'Organizer' : userRole}`} />
        </div>
      )}

      <div className="debug-info" style={{ fontSize: '12px', color: '#666', marginTop: '20px' }}>
        <p>Debug Info:</p>
        <p>Is Organizer: {isOrganizer ? 'Yes' : 'No'}</p>
        <p>Has Timer Control: {hasTimerControl ? 'Yes' : 'No'}</p>
        <p>User Role: {userRole || 'Unknown'}</p>
        <p>Teams Initialized: {isTeamsInitialized ? 'Yes' : 'No'}</p>
        <p>User ID: {debugContext.userId}</p>
        <p>Organizer ID: {debugContext.organizerId}</p>
        <p>Meeting ID: {debugContext.meetingId}</p>
        <p>Available Roles: {JSON.stringify(debugContext.roles)}</p>
      </div>
    </div>
  );
}

export default App;
